import React from "react"
import { Tanks } from "../Assets/data"
import '../Styles/tank_display.css'

const Tank = ({ match, tank, side, tankNum }) => {
    const team = match.teams[0].side === side ? match.teams[0] : match.teams[1]

    const tanksAlreadyPicked = side === 'A' ? match.tankPicks.attack.length : match.tankPicks.defense.length

    const notPickedTankImg = (match.turn < match.format.tankPickOrder.length && side == match.format.tankPickOrder[match.turn].charAt(1)) && parseInt(match.format.tankPickOrder[match.turn].charAt(0)) + tanksAlreadyPicked > tankNum ? 'https://cdn.discordapp.com/attachments/878081032992227390/880259741484449802/noImage.png' : 'https://cdn.discordapp.com/attachments/878081032992227390/880259463284686878/player_in_garage_alt.png'

    const theTank = Tanks.find(t => t.label === tank)
    const imgSrc = theTank ? theTank.customImg ? theTank.customImg : theTank.tanksggImg : notPickedTankImg
    const frameSrc = team.color === 'blue' ? 'https://cdn.discordapp.com/attachments/878081032992227390/882091136112541736/BlueTeam_Tank_Pick_v2.png' : 'https://cdn.discordapp.com/attachments/878081032992227390/882091137706385549/RedTeam_Tank_Pick_v2.png'
    return (

        <div className='container'>
            <h1 className='label'>{tank}</h1>
            <img className='imageOne tankImg img' src={frameSrc} />
            <img className='imageTwo tankImg img' src={imgSrc} />
        </div>

    )
}

const TankDisplay = ({ match, side }) => {
    const tanks = side === 'A' ? match.tankPicks.attack : match.tankPicks.defense
    const team = match.teams[0].side === side ? match.teams[0] : match.teams[1]
    return (
        <>
            <img className={`logo img`} src={team.logo} />
            <Tank side={side} tank={tanks[0]} match={match} tankNum={0} />
            <Tank side={side} tank={tanks[1]} match={match} tankNum={1} />
            <Tank side={side} tank={tanks[2]} match={match} tankNum={2} />
            <Tank side={side} tank={tanks[3]} match={match} tankNum={3} />
            <Tank side={side} tank={tanks[4]} match={match} tankNum={4} />
            <Tank side={side} tank={tanks[5]} match={match} tankNum={5} />
        </>
    )
}

export default TankDisplay