import React from "react"

//components
import TankDisplay from "../Components/TankDisplay"
//styles
import '../Styles/grid_spectate.css'
import '../Styles/fullscreendiv.css'
import '../Styles/tank_display.css'

const Spectate = ({ match }) => {
    return (
        <div className="container-spectate">
            <div className="Spacer"></div>
            <div className='container'>
                <img className='sideLogo' src="https://cdn.discordapp.com/attachments/878081032992227390/887092150477668402/attack.png" />
            </div>
            <div className="Spacer2"></div>
            <TankDisplay className="Attack-Tanks" match={match} side={'A'} />
            <TankDisplay className="Defense-Tanks" match={match} side={'D'} />
            <div className="Spacer3"></div>
            <div className='container'>
                <img className='sideLogo' src="https://cdn.discordapp.com/attachments/878081032992227390/887092145004093440/defense.png" />
            </div>
            <div className="Spacer4"></div>

        </div>
    )
}

export default Spectate