import { useMutation } from "@apollo/client"
import { RESET_ROUND, UNDO_LAST_TURN } from "./Queries"

const AdminPanel = ({ userKey }) => {
    const [resetRound] = useMutation(RESET_ROUND)
    const [undoTurn] = useMutation(UNDO_LAST_TURN)

    const doResetRound = (switchSides) => {
        resetRound({ variables: { userKey, switchSides } })
    }

    const doUndoTurn = () => {
        undoTurn({variables: {userKey}})
    }
    
    return (
        <>
            <button onClick={() => doUndoTurn()} className="Admin-Reset1"><h2>Undo</h2></button>
            <button onClick={() => doResetRound(false)} className="Admin-Reset2"><h2>RESET</h2></button>
            <button onClick={() => doResetRound(true)} className="Admin-Reset3"><h2>RESET AND SWITCH SIDES</h2></button>
        </>
    )
}

export default AdminPanel